import React from 'react';
import {Field, reduxForm } from 'redux-form';

class EditPassForm extends React.Component {
    render() {
        return (
            <form onSubmit={this.props.handleSubmit}>
                <label htmlFor="password"> </label><br/>
                <Field component="input"
                       name="password"
                       id="password"
                       type="password"
                       required/> <span className="pass_incorrect"> </span><br/>
                <label htmlFor="pass1"> </label><br/>
                <Field component="input"
                       name="pass1"
                       id="pass1"
                       type="password"
                       required/><br/>
                <label htmlFor="pass2">  </label><br/>
                <Field component="input"
                       name="pass2"
                       id="pass2"
                       type="password"
                       required/> <span className="pass_mismatch">  </span> <br/>
                <button type="submit"></button>
                <button onClick={() => {this.props.click('info')}}></button>
            </form>
        )
    }
}

EditPassForm = reduxForm({
    form: 'EditPass'
})(EditPassForm);

export default EditPassForm;


