
export function autoload(is_fetching, is_empty, dispatch, fetch, lenght, id = null) {
        let $point = $('.point');
        if (!$point[0]) {
            return;
        }
        let point = $point.offset().top;          //     
        let scroll_top = $(document).scrollTop(); //    (   )
        let height = $(window).height();   //  
        let load_flag = scroll_top + height >= point;   //    
        if (load_flag && !is_fetching && !is_empty) {
            dispatch(fetch(lenght, id));
        }
}


