import React from 'react';
import {Field, reduxForm } from 'redux-form';

class RegisterForm extends React.Component {
    constructor() {
        super(...arguments);
    }

    render() {
        return (
            <form onSubmit={this.props.handleSubmit} encType="multipart/form-data">
                <label htmlFor="login"></label><br/>
                <Field component="input"
                       name="login"
                       id="login"
                       type="text"
                       required
                       /><br/>
                <label htmlFor="name"></label><br/>
                <Field component="input"
                       name="name"
                       id="name"
                       type="text"
                       required
                       /><br/>
                <label htmlFor="surname"></label><br/>
                <Field component="input"
                       name="surname"
                       id="surname"
                       type="text"
                       required
                       /><br/>
                <label htmlFor="city"></label><br/>
                <Field component="input"
                       name="city"
                       id="city"
                       type="text"
                       /><br/>
                <label htmlFor="age"></label><br/>
                <Field component="input"
                       name="age"
                       id="age"
                       type="text"
                       /><br/>
                <label htmlFor="site">-</label><br/>
                <Field component="input"
                       name="site"
                       id="site"
                       type="text"
                       /><br/>
                <label htmlFor="email"> </label><br/>
                <Field component="input"
                       name="email"
                       id="email"
                       type="email"
                       /><br/>
                <label htmlFor="pass1"></label><br/>
                <Field component="input"
                        name="pass1"
                        id="pass1"
                        type="password"
                        required
                        /><br/>
                <label htmlFor="pass2"> </label><br/>
                <Field component="input"
                       name="pass2"
                       id="pass2"
                       type="password"
                       required
                /><br/>
                <button type="submit"></button>
            </form>
        )
    }
}

RegisterForm = reduxForm({
    form: 'Register'
})(RegisterForm);

export default RegisterForm;


